import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import GoogleMapsReact from 'google-map-react';

import Header from '../components/Header';

const backendUrl = process.env.GATSBY_BE_URL;
const mapsKey = process.env.GATSBY_MAPS_KEY;

const Wrapper = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 18px;
  background-color: #000;
  border: 2px solid #fff;
  border-radius: 100%;
  user-select: none;
  transform: translate(-50%, -50%);
  cursor: ${(props) => (props.onClick ? 'pointer' : 'default')};
  &:hover {
    z-index: 1;
  }
`;

const Marker = function ({ text, onClick }) {
  return (
    <Wrapper
      alt={text}
      onClick={onClick}
    />
  );
};

Marker.defaultProps = {
  onClick: () => { console.log('clicked'); },
};

Marker.propTypes = {
  onClick: PropTypes.func,
  text: PropTypes.string.isRequired,
};

const MapPage = function () {
  const [potholes, setPotholes] = useState([]);

  const defaultProps = {
    center: {
      lat: 33.79484,
      lng: -84.370456,
    },
    zoom: 18,
  };

  const potholeQuery = `query potholeQuery {
        potholes {
            name
            lat
            lng
        }
    }
    `;

  useEffect(() => fetch(backendUrl, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Accept: 'application/json',
    },
    body: JSON.stringify({
      query: potholeQuery,
    }),
  })
    .then((r) => r.json())
    .then((data) => {
      setPotholes(data.data.potholes);
    }), []);

  return (
    <div>
      <Header />
      <div style={{ height: '100vh', width: '100%' }}>
        <GoogleMapsReact
          // TODO: key in deploy?
          bootstrapURLKeys={{ key: mapsKey }}
          defaultCenter={defaultProps.center}
          defaultZoom={defaultProps.zoom}
        >
          {potholes.map((pothole) => (
            <Marker
              key={pothole.name}
              text="POTHOLE"
              lat={pothole.lat}
              lng={pothole.lng}
            />
          ))}
        </GoogleMapsReact>
      </div>
    </div>
  );
};

export default MapPage;
